/*Header */
.sticky {
  position: fixed;
  z-index: 99;
  width: 100%;
  top: 0;
  left: 0;
  -webkit-transition: all 0.2s ease;
  -o-transition: all 0.2s ease;
  transition: all 0.2s ease;
}

header {
  -webkit-transition: all 0.2s ease;
  -o-transition: all 0.2s ease;
  transition: all 0.2s ease;
}

header.ola-header .pull-left {
  float: left;
}
header.ola-header .pull-right {
  float: right;
}
header.ola-header .logo {
  width: 120px;
}

.primary-header {
