/* Jquery Tool Tip */
.jq-tooltip {
  display: none;
  position: absolute;
  border: 1px solid #333;
  background-color: #161616;
  border-radius: 5px;
  padding: 10px;
  color: #fff;
  font-size: 12px;
  font-size: 14px;
  z-index: 5;
  left: 0;
  bottom: 0;
}

/* Bootstrap tool tip */
.tooltip-inner {
  font-family: "Gentona";
  font-weight: 400;
  letter-spacing: 0.3px;
}

/*Css Tool tip*/
.cus-tooltip {
  position: relative;
}

.cus-tooltip .tooltiptext {
  visibility: hidden;
  min-width: 80px;
  background-color: rgba(61, 70, 77, 0.1);
  color: #141414;
  text-align: center;
  border-radius: 4px;
  padding: 5px 10px;
  position: absolute;
  z-index: 1;
  bottom: 5px;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
}

.cus-tooltip .tooltiptext::after {
  content: "";
  position: absolute;
  top: -10px;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent transparent rgba(61, 70, 77, 0.1) transparent;
}

.cus-tooltip:hover .tooltiptext {
  visibility: visible;
}

/*Ripple effect*/
.ripple {
  position: relative;
  overflow: hidden;
  z-index: 0;
}
.ripple:after {
  content: "";
  background: rgba(255, 255, 255, 0.3);
  display: block;
  position: absolute;
  border-radius: 50%;
  padding-top: 240%;
  padding-left: 240%;
  margin-top: -120%;
  margin-left: -120%;
  opacity: 0;
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
}
.ripple:active:after {
  padding-top: 0;
  padding-left: 0;
  margin-top: 0;
  margin-left: 0;
  opacity: 1;
  -webkit-transition: 0s;
  -o-transition: 0s;
  transition: 0s;
}

